import detectEthereumProvider from '@metamask/detect-provider'
import { hexToDec, decToHex } from 'hex2dec'

async function checkProvider () {
	const provider = await detectEthereumProvider()
	if (provider) {
		console.log('Ethereum successfully detected!')
		// From now on, this should always be true:
		// provider === window.ethereum
		// Access the decentralized web!
		// Legacy providers may only have ethereum.sendAsync
		const chainId = await provider.request({ method: 'eth_chainId' })
		console.log('chainId', chainId)
	}
}

const Transaction = async ({ to, val }) => {
	const { ethereum } = window
	await console.log("decToHex(21000)", decToHex('21000'))
	await console.log('checkProvider', checkProvider())
	
	const txParams = {
		// ignored by MetaMask
		nonce: '0x00',
		// customizable by user during MetaMask confirmation.
		gasPrice: '0x09184e72a000',
		// customizable by user during MetaMask confirmation.
		// gas: '0x2710',
		gas: await decToHex('21000'),
		// Required except during contract publications.
		to: to,
		// must match user's active address.
		from: ethereum.selectedAddress,
		// Only required to send ether to the recipient from the initiating external account.
		value: await decToHex(val),
		// Optional, but used for defining smart contract creation and interaction.
		data: '0x7f7465737432000000000000000000000000000000000000000000000000000000600057',
		// Used to prevent transaction reuse across blockchains. Auto-filled by MetaMask.
		chainId: '0x3'
	}
	// txHash is a hex string
	// As with any RPC call, it may throw an error
	const txHash = await ethereum.request({ method: 'eth_sendTransaction', params: [txParams] })
	console.log('txHash', txHash)
}

export default Transaction
